







































import Vue from 'vue'

export default Vue.extend({
  name: 'AddPathsDialog',

  data: () => ({
    selectedMappingItems: [],
    dialog: false,
    display: true,
    collectionItems:
      [
        'default',
        'default + rule',
        'default + rule + directed',
        'default + rule + directed + max length'
      ],
    collection: ''
  }),
  computed: {
  },
  methods: {
    changePaths: function () {
      this.dialog = false
      this.$emit('pathsDatasetChanged', this.collection)
    }
  }
})
