




















import Vue from 'vue'
import { Circle } from '../../models'

export default Vue.extend({
  name: 'CircleNode',
  props: {
    nodeData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
  }),
  computed: {
  },
  methods: {
    emit (circle: Circle) {
      this.$emit('nodeClicked', circle)
    },
    openWiki: function (circle: Circle) {
      const win = window.open('https://www.wikidata.org/wiki/' + circle.id, '_blank')
      if (win !== null) {
        win.focus()
      }
    }
  }
})
